@import "main-bx";
.fancybox__carousel .fancybox__slide .fancybox__content {
        background-color: transparent;
}
    
.brand-modal {

    &__close {
        @include fontProperty($accent_font, 700, 14px, 130%, #B8B8B8);
        letter-spacing: 0.05em;
        text-transform: uppercase;
        margin-bottom: 20px;
        display: flex;
        align-items: center;
        &:hover {
            color: $white;

            svg {
                stroke: $white;
            }
        }

        svg {
            width: 16px;
            height: 16px;
            stroke: #B8B8B8;
            transform: rotate(-180deg);
            margin-right: 20px;
        }
    }

    &__inner {
        display: flex;
    }

    &__title {
        @include fontProperty($accent_font, 700, 28px, 120%, $white)
    }

    &__item {
        &:first-child {
            max-width: 260px;
            width: 100%;
        }
        &:last-child {
            width: 100%;
            max-width: 600px;
            border: 1px solid #252525;
        }
    }

    &__logo {
        position: relative;
        width: 100%;
        max-width: 260px;
        height: 160px;
        display: flex;
        align-items: center;
        justify-content: center;

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: transparent;
            border: 1px solid $white;
        }

        img {
            max-width: 180px;
            height: 42px;
            object-fit: contain ;
        }
    }

    &__content {
        padding: 40px;
    }

    &__center {
        padding: 50px 0;
    }

    &__list {
        margin-top: 30px;
        list-style: none;
        li {
            display: flex;
            align-items: center;
            @include fontProperty($accent_font, 300, 18px, 120%, #B8B8B8);

            &:not(:last-child) {
                margin-bottom: 20px;
            }
        }
        svg {
            width: 100%;
            max-width: 24px;
            height: 24px;
            margin-right: 10px;
        }
    }

    &__bottom {
        display: flex;
        justify-content: space-between;
        border-top: 1px solid #252525;
    }
    
    &__link {
        @include fontProperty($accent_font, 300, 14px, 130%, $white);
        letter-spacing: 0.05em;
        text-transform: uppercase;
        transition: $trns;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 20px;
        width: 50%;

        &:first-child {
            border-right: 1px solid #252525;
        }

        &:hover {
            color: $color_yellow;
            
            svg {
                stroke: $color_yellow;
            }
        }

        svg {
            width: 16px;
            height: 16px;
            stroke: $white;
            transition: $trns;
            margin-left: 20px;
        }
    }
}

@include below(769px) {

    .fancybox__content {
        padding: 0 10px !important;
    }

    .brand-modal {
        &__inner {
            flex-wrap: wrap;
        }
        &__item {
            width: 100%;
            max-width: 100% !important;
        }

        &__logo {
            max-width: 100%;
        }

        &__title {
            font-size: 22px;
        }

        &__content {
            padding: 30px 20px;
        }
    }
}