@import "main-bx";

.contacts {
    position: relative;
    &__inner {
        background-color: #171717;
        padding: 145px 0 70px;


        @include below(1440px) {
            padding: 70px 0;
        }

        @include below(1124px) {
            padding: 50px 0;
        }
    }

    &__title {
        margin-bottom: 100px;
        margin-left: 17px;
    }
    
    &__wrap {
        // display: flex;
    }

    &__container {
        width: 100%;
        max-width: 525px;
        padding: 40px 0;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        border-top: 1px solid #252525;
        border-bottom: 1px solid #252525;
    }

    &__item {
        width: 40%;

        &-title {
            @include fontProperty($accent_font, 300, 20px, 130%, #7E7E7E);
            display: block;
            margin-bottom: 30px;
        }

        &-text {
            @include fontProperty($accent_font, 300, 20px, 130%, $white);
            display: block;
            &:last-child {
                margin-top: 12px;
            }
        }
    }
    &__social {
        width: 100%;
        margin-top: 54px;
    }

    &__map {
        flex: 1;
        position: absolute;
        top: 170px;
        right: 0;
        width: 100%;
        max-width: 950px;
        height: 450px;
        background-color: aqua;

        filter: grayscale(1);

        &:hover {
            filter: grayscale(0);
        }

        &-mark {
            position: relative;
            width: 140px;
            height: 46px;
            
            display: flex;
            align-items: center;
            justify-content: center;

            span {
                position: sticky;
                z-index: 10;
                color: $white;
                font-weight: 500;
                font-size: 14px;
            }

            img {
                position: absolute;
                right: 0;
                left: 0;
                z-index: 1;
            }
        }

        .ymaps-2-1-79-hint {
            box-shadow: none !important;
        }

        .ymaps-2-1-79-hint__text {
            background: transparent;
        }
    }
}


@include below(1600px) {
    .contacts {
        &__map {
            max-width: 700px;
        }
    }
}

@include below(1240px) {
    .contacts {
        &__map {
            max-width: 600px;
            height: 350px;
        }
    }
}

@include below(1024px) {
    .contacts {

        &__item {
            width: 100%;
        }
        &__map {
            position: relative;
            max-width: 600px;
            height: 350px;
            top: auto;
            right: auto;
        }
    }
}


@include below(769px) {
    .contacts {
        &__inner {
            padding: 40px 0 60px;
        }
        &__title {
            margin-left: 0;
            margin-bottom: 60px;
        }
        &__item {
            margin-bottom: 30px;
            &-text {
                display: inline-block;
                margin-right: 20px;
            }
        }

        &__social {
            margin-top: 40px;
            margin-bottom: 0;
        }

        &__map {
            max-width: 100%;
            margin-top: -30px;
            height: 400px;
        }
    }
}