@import "main-bx";

.honest {

    &__inner {
        background-color: $white;
        padding: 100px 0 215px;

        @include below(1440px) {
            padding: 70px 0;
        }

        @include below(1124px) {
            padding: 50px 0;
        }
    }

    &__btn {
        max-width: 320px;
        display: none;
        &_desk {
            display: flex;
        }
    }

    &__wrap {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        flex-wrap: wrap;
    }

    &__item {
        &:first-child {
            width: 40%;
        }
        &:last-child {
            width: 47%;
        }
    }

    &__text {
        padding-right: 70px;
        &:not(:last-child) {
            margin-bottom: 20px;
        }

        &:last-child {
            margin-bottom: 60px;
        }
    }

    &__sticker {
        margin: 20px 0 55px;
    }

    &__certificate {
        cursor: pointer;
        margin-bottom: -35px;
        transition: all .2s linear;

        &:hover {
            transform: scale(1.02);
        }
    }

    &__slider {
        display: none;
        width: 100%;
        max-width: 1920px;
        overflow: visible !important;

        .swiper-slide {
            opacity: 0.4;
        }

        .swiper-slide-active,
        .swiper-slide-next,
        .swiper-slide-duplicate-prev {
            opacity: 1;
        }

        &.fancybox__content .honest__close {
            display: block;
        }
    }

    &__arrow {
        position: relative;
        &.swiper-button-prev,
        &.swiper-button-next {
            width: 100px;
            max-width: 60px;
            height: 60px;
            border-radius: 50%;
            background-color: $color_yellow;
        }
        
        

        &.swiper-button-prev svg {
            transform: rotate(-180deg);
        }

        svg {
            width: 16px;
            height: 16px;
            stroke: $color_dark1;
        }

        &::after {
            content: none !important;
        }
    }

    &__close {
        position: absolute;
        top: -60px;
        right: 0;
        width: 100%;
        max-width: 250px;
        height: 250px;
        border-radius: 50%;
        border: 1px solid $white;
        cursor: pointer;
        font-weight: bold;
        font-size: 14px;
        line-height: 130%;

        text-align: center;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        color: #FFFFFF;
        display: none;
        &-inner {
            display: flex;
            align-items: center;
            justify-content: center;
            padding-top: 60px;
        }

        span {
            margin-right: 15px;
        }

        svg {
            width: 16px;
            height: 16px;
            stroke: $white;
        }
    }
}

@include below(769px) {
    .honest {
   
        &__inner {
            padding: 40px 0 60px;
            overflow: hidden;
        }

        &__certificate {
            display: none;
        }

        &__item {
            &:first-child,
            &:last-child {
                width: 100%;
            }
        }

        &__slider {
            display: block;
        }

        &__arrow {
            display: none !important;
        }


        &__btn {
            margin-top: 40px;
            display: flex;
            &_desk {
                display: none;
            }
        }
    }
}