@import "main-bx";

.nav {
    
    &__list {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__item {

        .active {
            color: $color_yellow;
        }
        a {

            @include fontProperty($accent_font, 300, 12px, 130%, $white);       

            @include below(1320px) {
                font-size: 10px;
                padding: 22px 12px;
            }

            @include below(1124px) {
                padding: 22px 7px;
            }
            display: block;
            padding: 22px 15px;
            letter-spacing: 0.05em;
            text-transform: uppercase;
            transition: $trns;
            
            &:hover {
                color: $color_yellow;
            }
        }
    }
}