@import "main-bx";

.client-category {
    &__wrap {
        display: flex;
        flex-wrap: wrap;
    }

    &__btn {
        display: block;
        padding: 12px 32px;
        border-radius: 52px;
        border: 1px solid $white;
        transition: $trns;

        color: $white;
        margin: 0 10px 10px 0;
        &:hover,
        &.active {
            color: $color_yellow;
            border-color: $color_yellow;
        }

        &:active {
            border-color: $white;
            color: $white;
        }
    }
}

@include below(769px) {
    .client-category {
        &__wrap {
            flex-wrap: nowrap;
            width: 1635px;
        }

        
    }
}