@import "main-bx";

.why-us {

    &__inner {

        @include below(1440px) {
            padding: 70px 0;
        }

        @include below(1124px) {
            padding: 50px 0;
        }
        background-image: url(../img/content/background/4.jpg);

        padding: 100px 0 190px;
    }

    &__title {
        margin-bottom: 100px;
    }

    &__icon {
        position: absolute;
        top: -130px;
        left: -120px;
        width: 340px;
        height: 340px;
    }

    &__heading {
        padding-left: 110px;

        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        &-wrap {
            width: 43%;
            &_left {
                padding-top: 74px;
            }
        }

        &-item {
            position: relative;
            &_left {
                margin-bottom: 210px;
            }

            &_right {
                margin-bottom: 210px;
            }
        }

        &-title {
            font-weight: 400;
            font-size: 36px;
            line-height: 100%;
            color: $white;
            margin-bottom: 32px;

            span {
                display: block;
            }
        }

        &-text {
            color: #b8b8b8;
            line-height: 120%;
        }
    }
}

@include below(769px) {
    .why-us {
        &__inner {
            background-image: url(../img/content/background/m4.jpg);
            padding: 40px 0 60px;
        }

        &__heading {
            padding-left: 50px;
            display: block;

            &-item {
                &_left, &_right {
                    margin-bottom: 140px;
                }
            }

            &-title {
                font-size: 22px;
                margin-bottom: 16px;
            }

            &-wrap {
                width: 100%;
                &:not(:last-child) {
                    margin-bottom: 140px;
                }
            }

            &-wrap_left {
                padding-top: 0;
            }
        }

        &__icon {
            width: 200px;
            height: 200px;
            top: -100px;
            left: -70px;
        }
    }
}