@import "main-bx";

.phone {
    &__item {
        display: flex;
        align-items: center;
        @include fontProperty($accent_font, 300, 20px, 130%, $white);
        transition: $trns;
        &:first-child {
            margin-right: 52px;

            @include below(1024px) {
                margin-right: 0;
                margin-bottom: 12px;
            }
        }

        &:hover {
            color: $color_yellow;
        }

        &:hover svg {
            stroke: $color_yellow;
        }

        svg {
            width: 24px;
            height: 24px;
            fill: transparent;
            stroke: $white;
            margin-right: 12px;
            transition: $trns;
        }
    }
}