@import "main-bx";

.work-results {
    &__inner {
        background-image: url(../img/content/background/3.jpg);

        padding: 100px 0 70px;
        overflow: hidden;

        @include below(1440px) {
            padding: 70px 0;
        }
    
        @include below(1200px) {
            padding: 50px 0;
        }

        @include below(769px) {
            padding: 40px 0 60px;
        }

    }

    &__head {
        position: relative;
        width: 100%;
        max-width: 840px;
        margin-bottom: 60px;

        @include below(1440px) {
            margin-bottom: 50px;
        }
    
        @include below(1200px) {
            margin-bottom: 30px;
        }
    }

    &__text {
        position: absolute;
        bottom: 20px;
        right: 200px;
        width: 275px;

        @include below(1200px) {
            position: sticky;
            margin-top: 14px;
        }
    }

    &__link {
        display: none;
        align-items: center;
        justify-content: center;
        margin-top: 30px;
        @include fontProperty($accent_font, 700, 14px, 130%, $color_dark1);
        letter-spacing: 0.05em;
        text-transform: uppercase;
        @include below(769px) {
            display: flex;
        }
        svg {
            width: 16px;
            height: 16px;
            stroke: $color_dark1;
            margin-left: 20px;
        }
    }
}
