@import "main-bx";

.success {
    display: none;
    
    width: 100%;
    text-align: center;
    &.fancybox__content {
        width: 100%;
        max-width: 860px;
        padding: 90px 20px 100px;
        box-shadow: 0px 4px 30px #1F1F1F;
        background: #1F1F1F;
        border-radius: 5px;

        @include below(769px) {
            padding: 50px 20px !important;
        }
    }
    &.fancybox__content>.carousel__button.is-close {
        top: 20px;
        right: 20px;
    }
    &__inner {
        display: flex;
        flex-direction: column;
        align-items: center;

        
    }
    
    &__title {
        font-weight: normal;
        font-size: 36px;
        line-height: 100%;
        margin-bottom: 40px;
        color: $white;
    }

    &__text {
        font-size: 16px;
        line-height: 140%;
        font-weight: 300;
        color: #7e7e7e7e;
        text-align: center;
    }

    &__btn {
        padding: 16px 52px;
        border-radius: 30px;
    }
}