@import "main-bx";

.btn {
    @include fontProperty($accent_font, 600, 14px, 140%);
    cursor: pointer;
    transition: all .2s linear;
    position: relative;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    &:hover {
        &::after {
            width: 100%;
        }
    }

    &__text {
        position: sticky;
        z-index: 10;
        transition: $trns;
    }

    &__icon {
        position: sticky;
        z-index: 10;
        svg {
            width: 16px;
            height: 16px;
            margin-bottom: -3px;
            stroke: #1F1F1F;
        }
    }

    &_white {
        padding: 19px 0;
        background-color: $white;

        &:hover {
            background-color: $color_yellow;
        }

        &:active {
            background-color: $white;
        }
    }

    &_spec {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 21px 22px 21px 30px;
        position: relative;
        width: 100%;
        max-width: 227px;

        

        &::after {
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            z-index: 1;
            width: 60px;
            height: 100%;
            border-radius: 30px;
            transition: $trns;
            border: 1px solid transparent;
            
            @include below(769px) {
                width: 100%;
            }
        }
    }

    &_yellow {

        @include below(769px) {
            color: $color_dark1;
        }

        &:hover {
            .btn__text {
                color: $color_dark1;
            }
        }
        &::after {
            background-color: $color_yellow;
        }

        &:active::after  {
            box-shadow: 0px 0px 15px -2px rgba(223,240,9,0.78);
        }
    }

    &_dark {
        
        svg {
            stroke: $white;
        }

        &:hover {
            .btn__text {
                color: $white;
            }
        }
        &::after {
            background-color: $color_dark1;
            border-color: #1F1F1F;
        }

        &:active::after  {
            background-color: $white;
        }

        &:active .btn__text {
            color: $color_dark1;
        }

        &:active svg {
            stroke: $color_dark1;
        }
    }

    &__preloader {
        position: absolute;
        top: 8px;
        left: 0;
        right: 0;
        margin: auto;
        transition: all .2s;
        opacity: 0;
    }

    &:disabled {
        opacity: 0.7;
        cursor: no-drop;
        .btn__text {
            opacity: 0;
        }

        .btn__icon {
            opacity: 0;
        }

        .btn__preloader {
            opacity: 1;
            z-index: 100;
        }

        &::after {
            width: 100%;
        }
    }

    .disabled {
        
    }
}