@import "main-bx";

body .fancybox {
    &__content {
        background-color: #1f1f1f;
    }
}

.fancybox__nav .carousel__button {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: $color_yellow;

    transition: $trns;

    &:hover {
        box-shadow: 0px 0px 15px -2px rgba(223,240,9,0.78);
    }
}

.fancybox__slide {
    overflow: hidden !important;
}

.fancybox__backdrop {
    background: rgba(0, 0, 0, 0.9) !important;
    backdrop-filter: blur(50px);
}

