@import "main-bx";


.brands {
    &__wrap {
        display: flex;
        flex-wrap: wrap;
    }

    &__item {
        position: relative;
        width: 20%;
        height: 160px;
        display: flex;
        justify-content: center;
        align-items: center;
        filter: brightness(0) invert(1);
        transition: $trns;
        cursor: pointer;
        border: 1px solid rgba(0, 0, 0, .1);
        &:hover {
            filter: grayscale(0);
            &::before {
                opacity: 1;
            }

            .brands__plus {
                opacity: 1;
            }
        }

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: transparent;
            border: 1px solid $white;
            opacity: 0;
            transition: $trns;
        }

        img {
            max-width: 180px;
            object-fit: contain;
        }
    }

    &__plus {
        position: absolute;
        bottom: 0;
        right: 0;
        opacity: 0;
        transition: $trns;

        width: 40px;
        height: 40px;
    }
}

@include below(1124px) {
    .brands {
        &__item {
            width: 25%;
        }
    }
}

@include below(1000px) {
    .brands {
        &__item {
            width: 33.33333%;
        }
    }
}


@include below(769px) {
    .brands {
        &__item {
            width: 50%;

            height: 110px;

            img {
                max-width: 120px;
            }
        }

       
    }
}