@import "main-bx";

.accordion {
    position: relative;
    min-height: 500px;
    
    &__head {
        width: 100%;
        max-width: 375px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        cursor: pointer;
        padding: 32px 40px;
        border: 1px solid rgba(255, 255, 255, .2);
        transition: $trns;

        

        svg {
            width: 24px;
            height: 24px;
            stroke: $color_yellow;
            opacity: 0;
            transition: $trns;

            @include below(1024px) {
                opacity: 1;
                stroke: $white;
            }
        }

        &.active {
            border-color: #FFFFFF;
            svg {
                opacity: 1;

                @include below(1024px) {
                    transform: rotate(90deg);
                    stroke: $color_yellow;
                }
            }

            .accordion__title {
                color: $color_yellow;
            }
        }

        &:hover {
            .accordion__title {
                color: $color_yellow;
            }
        }

        @include below(1320px) {
            max-width: 230px;
        }

        @include below(1024px) {
            max-width: 100%;
        }
    }

    &__title {
        color: $white;
        transition: $trns;
    }

    &__body {
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        max-width: 924px;
        height: 100%;
        padding: 55px 115px 55px 65px;
        border: 1px solid #FFFFFF;
        display: none;
        transition: $trns;
        &.active {
            display: block;
        }

        @include below(1320px) {
            max-width: 870px;
        }

        @include below(1124px) {
            max-width: 693px;
            padding: 55px 15px;
        }

        @include below(1024px) {
            max-width: 100%;
            position: relative;
        }
    }

    &__stage {
        position: relative;
    }
}


@include below(600px) {
    .accordion {
        &__head {
            padding: 24px 20px;
        }

        &__body {
            padding: 30px 25px;
        }
    }
}