@import "main-bx";

.form {
    &__wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;

        &_block {
            display: block;
        }

        @include below(769px) {
            flex-direction: column;
            align-items: flex-start;
        }
    }

    &__box {
        position: relative;
        flex: 1; 
        margin-right: 45px;
        border-bottom: 1px solid #7E7E7E;
        display: block;

        @include below(769px) {
            width: 100%;
            margin-right: 0;
            margin-bottom: 30px;
        }

        &.error {
            border-color: $color_red;
        }

        &.error .form__input::placeholder {
            color: $color_red;
        }

        &.error .form__error {
            opacity: 1;
        }

        &_mr {
            margin-right: 0;
            margin-bottom: 40px;
        }
    }

    &__input {
        padding: 12px 0;
        width: 100%;
        @include fontProperty($accent_font, 300, 18px, 120%, $white);

        &_dark {
            color: $color_dark1;
        }
    }

    &__error {
        position: absolute;
        display: block;
        bottom: -50px;
        font-size: 12px;
        left: 0;
        color: $color_red;
        width: 100%;
        padding: 3px 0;
        opacity: 0;
        transition: $trns;

        &_bottom {
            bottom: -35px;
        }

        @include below(769px) {
            bottom: -25px;
        }
    }


    
}
