@import "main-bx";

.calculation-step {

    &__container {
        position: relative;
        padding-bottom: 60px;
        border-bottom: 1px solid #1F1F1F;
        margin-bottom: 20px;

        &::before {
            content: "";
            position: absolute;
            top: -40px;
            left: 0;
            width: 100%;
            max-width: 684px;
            height: 1px;
            background-color: $color_dark1;
        }
    }

    &__wrap {
        display: none;
        &.active {
            display: block;
        }
    }

    &__row {
        display: flex;
        flex-wrap: wrap;

        &_hide {
            display: none;
            margin-top: 20px;
        }
    }

    &__error {
        position: absolute;
        left: 0;
        bottom: -30px;
    }

    &__counter {
        position: absolute;
        top: -55px;
        right: 0;

        display: flex;
        align-items: center;

        &-numb {
            @include fontProperty($accent_font, 400, 36px, 100%, $white);
            color: $white;
            display: flex;
            align-items: center;
            margin-right: 12px;
            &::after {
                content: "";
                width: 62px;
                height: 1px;
                background-color: $white;
                margin: 0 12px;
            }
        }

        &-total {
            @include fontProperty($accent_font, 300, 14px, 130%, #7E7E7E);
        }
    }

    &__label {
        cursor: pointer;
        &:not(:last-child) {
            margin-right: 16px;
        }
        &:hover {
            .calculation-step__radio {
                color: $color_yellow;
                border-color: $color_yellow;
            }
        }
    }

    input {
        &:checked + .calculation-step__radio {
            color: $color_dark1;
            background-color: $color_yellow;
            border-color: $color_yellow;
        }
    }

    &__radio {
        @include fontProperty($accent_font, 300, 18px, 120%, $white);
        display: inline-block;
        padding: 25px 40px;
        border-radius: 52px;
        border: 1px solid $white;
        transition: $trns;
    }

    &__button {
        display: flex;
        align-items: center;
        justify-content: flex-end;
    }

    &__submit {
        max-width: 270px;
        margin-left: 70px;
        color: $white;
    }

    &__arrow {
        color: $white;
        padding: 20px;
        svg {
            width: 16px;
            height: 16px;
            stroke: $white;
        }
    }

    &__prev {
        svg {
            transform: rotate(-180deg);
        }
    }

    &__next {
        margin-left: 70px;
        svg {
            margin-left: 10px;
        }
    }


    &__error {
        color: $color_red;
        transition: $trns;
        opacity: 0;

        &.error {
            opacity: 1;
        }
    }


    &__input {
        background-color: $white;
        padding: 20px;
    }

    &__title {
        @include fontProperty($accent_font, 400, 36px, 100%, $white);
        margin-bottom: 12px;
    }

    &__text {
        @include fontProperty($accent_font, 300, 16px, 130%, #7E7E7E);
        margin-bottom: 40px;
    }

    .selectra-container {
        margin-right: 45px;
    }

    .selectra-handler-container {
        width: 100%;
        border-bottom: 1px solid $white;
        margin-right: 45px;
    }

    .selectra-input {
        background-color: transparent;
        color:  $white;
        padding: 12px 0;
        width: 100%;
    }

    .selectra-handler-icon {
        top: 12px;

        svg {
            fill: $color_yellow;
        }
    }


    &__site {
        border-bottom: 1px solid $white;
        width: 60%;

        input::placeholder {
            color: $white;
        }
    }

    &__otherRegion {
        display: block;
        border-bottom: 1px solid $white;
        width: 50%;
    }
}

@include below(769px) {
    .calculation-step {

        &__container {
            padding-bottom: 20px;
        }
        
        &__wrap {
            position: relative;
            z-index: 10;
        }

        &__row {
            .form__box {
                flex: auto;
                width: 100%;
            }
        }

        &__counter {
            &-numb {
                font-size: 22px;

                &::after {
                    width: 40px;
                }
            }
        }

        &__title {
            font-size: 22px;
        }
    
        &__label {
            margin-bottom: 20px;
        }

        &__error {
            bottom: -15px;
        }

        &__button {
            justify-content: space-between;
        }

        .selectra-container {
            margin-bottom: 20px;
            margin-right: 0;
        }

        &__site,
        .selectra-container {
            width: 100%;
        }

        &__radio {
            padding: 25px 32px;
        }

        &__otherRegion {
            width: 100%;
        }
    }
}

@include below(600px) {
    .calculation-step {
        &__submit {
            max-width: 100%;
            margin-left: 0;
        }
    }
}

@include below(360px) {
    .calculation-step {
        &__radio {
            padding: 20px;
        }
    }
}