@import "main-bx";

.our-clients {
    &__inner {
        background-image: url(../img/content/background/6.jpg);

        padding: 100px 0 135px;

        @include below(1440px) {
            padding: 70px 0;
        }

        @include below(1124px) {
            padding: 50px 0;
        }
    }

    &__title {
        margin-bottom: 40px;
    }

    &__category {
        margin-bottom: 60px;
    }

    &__brand {
        margin-bottom: 182px;

        @include below(1440px) {
            margin-bottom: 120px;
        }

        @include below(1124px) {
            margin-bottom: 80px;
        }
    }

    &__bottom {
        display: flex;

        &-title {
            @include fontProperty($accent_font, 700, 36px, 100%, $white);
            margin-bottom: 20px;
        }

        &-text {
            @include fontProperty($accent_font, 300, 16px, 120%, $color_grey);
            margin-bottom: 30px;
        }
    }
    &__link {
        margin-right: 85px;
        img {
            max-width: 178px;
            height: 178px;
            object-fit: contain;
        }
    }

    &__btn {
        max-width: 260px;
    
        color: $white;
    }

    &__call {
        display: none;
        align-items: center;
        justify-content: center;
        @include fontProperty($accent_font, 700, 18px, 130%, $white);
        letter-spacing: 0.05px;
        text-transform: uppercase;
        text-align: center;
        margin: 40px auto 0;
        padding: 20px 0;
        svg {
            width: 16px;
            height: 16px;
            stroke: $white;
            margin-left: 20px;
        }
    }
}

@include below(769px) {
    .our-clients {

        &__inner {
            padding: 20px 0 60px;
        }

        &__category {
            overflow: auto;
        }

        &__bottom {
            display: none;
        }

        &__brand {
            margin-bottom: 0;
        }

        &__call {
            display: flex;
        }
    }
}