@import "main-bx";

.modal {
    display: none;
    width: 100%;
    
    .form__wrap {
        position: relative;
        flex-direction: column;
        align-items: flex-end;
        max-width: 420px;
        margin: 0 auto;
    }
    .form__error {
        text-align: left;
        bottom: -30px;
    }
    .form__box  {
        width: 100%;
        margin-right: 0;
        margin-bottom: 30px;
    }
    &__body {
        width: 100%;
    }

    &__btn {
        max-width: 260px;
        color: $white;
    }

    .carousel__button {
        position: absolute;
        top: 20px !important;
        right: 20px !important;

        svg {
            color: #fff;
        }
    }
}
