@import "main-bx";

.stage {

    &:not(:last-child) {
        @include below(650px) {
            margin-bottom: 30px;
        }
    }

    &__head {
        @include fontProperty($accent_font, 300, 14px, 140%, $white);
        cursor: pointer;
        width: 100%;
        max-width: 200px;
        padding-left: 40px;
        opacity: 0.3;
        margin-bottom: 40px;
        transition: $trns;
        &-numb {
            position: relative;
            display: flex;
            align-items: center;
            justify-content: space-between;
           
            &::before {
                content: "";
                position: absolute;
                left: -40px;
                width: 8px;
                height: 1px;
                background-color: $white;
                opacity: 0.3;
                transition: $trns;
            }
        }

        &:hover {
            opacity: 1;
        }

        &.active {
            opacity: 1;
        }

        &.active .stage__head-numb::before {
            width: 30px;
            opacity: 1;
        }

        @include below(650px) {
            display: none;
        }
    }

    &__body {
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        max-width: 520px;
        display: none;
        &.active {
            display: block;
        }

        @include below(769px) {
            max-width: 420px;
        }

        @include below(650px) {
            display: block;
            position: relative;
               max-width: 100%;
        }
    }

    &__title {
        display: block;
        @include fontProperty($accent_font, 400, 36px, 100%, $white);
        margin-bottom: 50px;
        span {
            margin-right: 30px;
        }

        @include below(600px) {
            font-size: 22px;
            margin-bottom: 20px;
        }
    }

    &__row {
        display: flex;
        align-items: flex-start;
        font-weight: 300;
        color: $white;

        &:not(:last-child) {
            margin-bottom: 25px;

            @include below(600px) {
                margin-bottom: 20px;
            }
        }
        svg {
            width: 100%;
            max-width: 22px;
            height: 22px;
            margin-right: 16px;
        }
    }
}