@import "main-bx";

$slideBorder: 1px solid #1F1F1F;

.results-slider {
    &.swiper {
        overflow: visible;
    }

    .results-slider-pagination {
        position: absolute;
        bottom: auto;
        left: 0;
        right: 0;
        top: -45px;
        text-align: center;
        display: none;
    }
    .results-slider-pagination .swiper-pagination-bullet.swiper-pagination-bullet-active {
        border: 1px solid #1F1F1F;
        background-color: #1F1F1F;
    }

    .results-slider-pagination .swiper-pagination-bullet {
        border: 1px solid #1F1F1F ;
        background: transparent;
    }

    
    &__arrow {
        
        &.swiper-button-prev {
            left: -30px;
        }

        &.swiper-button-next {
            right: -30px;
        }

        &.swiper-button-prev,
        &.swiper-button-next {
            width: 100%;
            max-width: 60px;
            height: 60px;
            background-color: #1F1F1F;
            border-radius: 50%;
            &::after {
                content: none;
            }
        }

        svg {
            width: 16px;
            height: 16px;
            stroke: $white;
        }

        &.swiper-button-prev {

            svg {
                transform: rotate(-180deg);
                width: 16px;
                height: 16px;
            }
        }
    }

    &__item {
        opacity: .5;
        overflow: hidden;
        &.swiper-slide-active,
        &.swiper-slide-next {
            opacity: 1;
        }
        @media (min-width:769px) {
            &:hover {
                &::before {
                    transform: scale(1);
                    opacity: 1;
                }
    
                .results-slider__img {
                    transform: translateY(150%);
                    overflow: hidden;
                    z-index: -1;
                    opacity: 0;
                }
                
                .results-slider__bottom,
                .results-slider__list {
                    opacity: 1;
                }
            }
        }
        &:first-child {
            border-left: $slideBorder;
        }
        height: 700px !important;
        border-right: $slideBorder;
        border-top: $slideBorder;
        border-bottom: $slideBorder;
        cursor: pointer;
        position: relative;
        

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            width: 100%;
            height: 100%;
            background-color: $white;
            z-index: 1;
            opacity: 0;
            transition: all .5s linear;
        }
    }

    &__top {
        position: relative;
        z-index: 10;
        padding: 50px 80px 0 120px;
    }

    &__bottom {
        position: relative;
        z-index: 10;
        border-top: 1px solid #1F1F1F;
        padding: 30px 80px;
        display: flex;
        justify-content: flex-end;
        opacity: 0;
        transition: all .5s linear;
    }

    &__img {
        position: absolute;
        bottom: 0;
        right: 0;
        transition: all .5s linear;
    }
    
    &__title {
        @include fontProperty($accent_font, 400, 36px, 100%, $color_dark1);
        margin-bottom: 20px;
        padding-right: 50px;
        min-height: 73px;
    }

    &__text {
        min-height: 63px;
    }

    &__list {
        border-top: 1px solid #DFF009;
        padding: 30px 0 90px;
        opacity: 0;
        transition: all .5s linear;
    }

    &__result {
        display: flex;
        align-items: center;
        
        &:not(:last-child) {
            margin-bottom: 36px;
        }
    } 

    &__key {
        display: block;
        @include fontProperty($accent_font, 400, 14px, 130%, $color_dark1);
        width: 200px;
        margin-right: 70px;
    }

    &__val {
        @include fontProperty($accent_font, 900, 40px, 100%, $color_dark1);
    }

    &__btn {
        color: $color_dark1;
    }
}

@include below(1320px) {
    .results-slider {
        &__item {
            height: 760px !important;
        }
    }
}

@include below(1124px) {
    .results-slider {
        &__top {
            padding: 50px 50px 0;
        }
    }
}

@include below(1000px) {
    .results-slider {
        &__item {
            height: 800px !important;
        }
    }
}

@include below(769px) {
    .results-slider {
        .results-slider-pagination {
            display: block;
        }
        &__title {
            font-size: 20px;
            line-height: 1;
            padding-left: 0;
            min-height: auto;
            padding-left: 0;
        }

        &__item {
            height: auto !important;
            background-color: $white;
            border: 1px solid #1F1F1F !important;
        }
  
        &__top {
            padding: 20px 20px 0;
        }
        &__img {
            position: relative;
            padding-left: 20px;
        }
        &__text {
            display: none;
        }

        &__list {
            border: 0;
            opacity: 1;
            padding: 0 0 30px;
        }

        &__result {
            &:not(:last-child) {
                margin-bottom: 20px;
            }
        }
        &__val {
            font-size: 24px;
        }

        &__bottom {
            padding: 10px 20px;
            opacity: 1;
        }

        &__arrow {
            display: none !important;
        }
    }
}

@include below(600px) {
    .results-slider {
        &__btn {
            max-width: 100%;
        }
    }
}