@import "main-bx";

.intro-form {
    &__head {
        width: 100%;
        max-width: 452px;
        margin-bottom: 25px;

        @include below(769px) {
            margin-bottom: 0;
        }
    }

    &__title {
        @include fontProperty($accent_font, 700, 36px, 100%, $color_yellow);
        margin-bottom: 22px;

        @include below(769px) {
            font-size: 28px;
            line-height: 120%;
            margin-bottom: 20px;
        }
    }

    &__text {
        @include fontProperty($accent_font, 300, 16px, 130%, $color_grey);

        @include below(769px) {
            font-size: 14px;
        }
    }

    &__btn {
        color: $white;
        @include below(769px) {
            max-width: 100%;
        }
    }
}