@import "main-bx";

.footer {
    &__inner {
        padding: 30px 0;
        background-color: $color_dark1;
        background-color: #171717;
    }

    &__row {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 40px;
        border: 1px solid #7E7E7E;

        @include below(769px) {
            flex-direction: column-reverse;
            padding: 0;
            
        }
    }

    &__copy {
        @include fontProperty($accent_font, 300, 16px, 130%, #7E7E7E);
    }

    &__nav {
        padding: 20px;
        .nav__list {
            @include below(769px) {
                flex-wrap: wrap;
            }
        }

        .nav__item {
            @include below(600px) {
                width: 50%;
                a {
                    padding: 12px 0;
                }
            }
        }
        
    }

    &__copy {
        @include below(769px) {
            padding: 20px;
            border-top: 1px solid #7E7E7E;
            width: 100%;
        }
    }
}