@import "main-bx";

.team {
    &__inner {
        background-image: url(../img/content/background/10.jpg);

        padding: 110px 0 140px;
        overflow: hidden;

        @include below(1440px) {
            padding: 70px 0;
        }

        @include below(1124px) {
            padding: 50px 0;
        }
    }

    &__title {
        margin-bottom: 90px;
        &_bottom {
            margin-bottom: 60px;
        }

        @include below(1440px) {
            margin-bottom: 70px;
        }

        @include below(1124px) {
            margin-bottom: 40px;
        }
    }

    &__slider {
        margin-bottom: 160px;
        @include below(1440px) {
            margin-bottom: 120px;
        }

        @include below(1124px) {
            margin-bottom: 80px;
        }
        &.swiper {
            overflow: visible;
        }

        &-item {
            position: relative;
            cursor: pointer;
            &::before {
                content: "";
                display: block;
                position: absolute;
                top: 10px;
                left: 10px;
                width: 230px;
                height: 439px;
                border: 1px solid $color_yellow;
                transition: $trns;
                opacity: 0;
            }

            &:hover {
                &::before {
                    opacity: 1;
                }

                .team__slider-over {
                    opacity: 1;
                }
            }
        }

        &-arrow {
            border-radius: 50%;
            border: 1px solid $color_yellow;
            &.swiper-button-next,
            &.swiper-button-prev {
                width: 60px;
                height: 60px;
            }

            &.swiper-button-prev svg {
                transform: rotate(-180deg);
            }

            &::after {
                content: none !important;
            }
            svg {
                width: 16px;
                height: 16px;
                stroke: $color_yellow;
            }

        }

        &-over {
            padding: 30px;
            transition: $trns;
            opacity: 0;
            
        }

        &-name {
            @include fontProperty($accent_font, 700, 14px, 130%, $color_dark1);
            letter-spacing: 0.05em;
            text-transform: uppercase;
            margin-bottom: 12px;
        }
        &-pos {
            @include fontProperty($accent_font, 700, 14px, 130%, $color_dark1);
        }
    }

    &__questions {
        width: 100%;
        max-width: 540px;
        height: 70px;
        margin: 0 !important;
        margin-right: 110px;
        .swiper-slide {
            @include fontProperty($accent_font, 700, 28px, 120%, $color_dark1);
        }
    }

    &__bottom {
        display: flex;
        &-left {
            width: 100%;
            max-width: 620px;
            @include fontProperty($accent_font, 300, 20px, 130%, $color_dark1);
        }
    }

    &__btn {
        max-width: 260px;
        transform: translateX(-30px);
        margin-top: 40px;
    }
}

@include below(769px) {
    .team {
        &__inner {
            padding: 40px 0 60px;
        }

        &__title {
            margin-bottom: 30px;
        }
        &__slider {
            margin-bottom: 80px;
            &-item {
                opacity: 0.5;
                &.swiper-slide-active {
                    opacity: 1;
                }
            }
            
            &-item::before {
                width: 97%;
                height: 100%;
            }
            &-arrow {
                display: none !important;
            }

            
        }

        &__bottom {
            flex-direction: column;

            &-left {
                margin-top: 20px;
            }
        }

        &__questions .swiper-slide {
            font-size: 22px;
        }

        &__btn {
            transform: translateX(0);
        }
    }
}

@include below(600px) {
    .team {
       &__btn {
           max-width: 100%;
       }
        
       &__questions .swiper-slide {
           font-size: 20px;
       }
    }
}

@include below(380px) {
    .team {
       &__slider {
            &-item::before {
                width: 92%;
            }
       }
    }
}