@import "main-bx";

.intro {
    position: relative;
    &__form {
        position: absolute;
        bottom: 100px;
        left: 0;
        right: 0;
        z-index: 100;

        width: 100%;
        max-width: 920px;
        margin: 0 auto;
    }



    &__slider {
        .intro-swiper-pagination {
            position: absolute;
            text-align: center;
            z-index: 100;
            bottom: 35px;
        }
        .swiper-pagination-bullet {
            width: 10px;
            height: 10px;
            background-color: transparent;
            border: 1px solid rgba(255, 255, 255, 0.5);
            transition: $trns;
        }
        .swiper-pagination-bullet.swiper-pagination-bullet-active {
            width: 10px;
            height: 10px;
            background-color: rgba(255, 255, 255, 0.5);
        }
    }

    &__item {
        position: relative;

        img {
            height: 970px;

            @include below(1320px) {
                height: 830px;
            }

            @include below(769px) {
                height: 1000px;
            }
        }
    }

    &__overwrap {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 100;
    }
    &__content {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        padding-top: 260px;

        &_first {
            padding-left: 50px;
            padding-right: 50px;
        }
    }


    &__title {
        display: block;
        strong {
            display: none;
        }
        @include fontProperty($accent_font, 400, 120px, 100%, $white);
        text-transform: uppercase;

        @include below(1320px) {
            font-size: 80px;
        }

        
        @include below(1124px) {
            font-size: 60px;
        }

        &_transparent {
            color: transparent;
            -webkit-text-stroke: 1px $white;
            
        }

        &_first {
            margin-left: 150px;
            margin-right: 50px;
        }

        &_second {
            margin-right: 72px;
        }

        &_center {
            text-align: center;
            width: 100%;
        }
    }

    &__text {
        @include fontProperty($accent_font, 300, 20px, 130%, $white);
        width: 100%;
        &_first {
            
            max-width: 445px;
        }

        &_second {
            max-width: 585px;
            margin-right: 30px;
            margin-left: 40px;
        }

        &_third {
            max-width: 277px;
        }
    }
    
}

@include below(1024px) {
    .intro {
        &__title {
            font-size: 60px;
            line-height: 100%;
        }

        &__form {
            max-width: 700px;
        }
    }
}

@include below(769px) {
    .intro {

        &__content {
            padding-top: 120px;
            flex-direction: column;
            align-items: flex-start;
            &_first {
                padding-left: 0;
                padding-right: 0;
            }
        }
    

        &__title {
            margin-bottom: 5px;
            strong {
                display: inline-block;
            }
            span {
                display: block;
            }

           &_first {
               order: 1;
               margin-left: 0;
               margin-right: 0;
           }

            &_transparent {
               order: 2;
                word-wrap: break-word;
            }
            &_second {
                margin-right: 0;
                order: 1;
            }

            &_last {
                order: 3;
                margin-bottom: 25px;
            }

            &_center {
                text-align: left;
                order: 2;
            }
        }

        &__text {
            font-size: 16px;
            line-height: 130%;

            &_first {
                order: 4;
                margin-bottom: 16px;
            }

            &_second {
                order: 5;
                margin-left: 0;
            }

            &_third {
                order: 3;
                margin-top: 40px;
                padding-right: 30px;
            }
        }

        &__form {
            max-width: 375px;
            padding: 0 20px;
            bottom: 50px;
        }

        &__slider {
            .intro-swiper-pagination {
                bottom: auto;
                top: 80px;
            }
        }
    }
}