@import "main-bx";

*, *::before, *::after {
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

h1, h2, h3, h4, h5, h6, ul, p {
    margin: 0;
    padding: 0;
}

.container {
    width: 100%;
    max-width: map-get($container, xl);
    margin: 0 auto;

    @include below(1320px) {
        max-width: 1100px;
    }

    @include below(1124px) {
        max-width: 975px;
        padding: 0 25px;
    }

    @include below(769px) {
        max-width: 100%;
    }

    @include below(575px) {
        padding: 0 15px;
    }
}

body {
    position: relative;
    font-family: $body_font;
    font-size: 16px;
    background-color: $color_dark1;
    width: 100%;
    max-width: $body_width;
    margin: 0 auto !important;
    color: $black;
    overflow-x: hidden;
}

.main {
    
} 


ul {
    list-style-type: none;
}

a {
    text-decoration: none;
}

:focus {
    outline-style: none;
    outline-width: 0 !important;
    outline-color: none !important;
}

fieldset {
    margin: 0;
    padding: 0;
    border: 0;
}

input,
button,
textarea,
select {
    background: transparent;
    border: none;
    padding: 0;
}

button {
    cursor: pointer;
}

img {
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    object-fit: cover;
}

svg {
    transition: all .2s linear;
}

.lds-dual-ring {
    display: inline-block;
    width: 30px;
    height: 30px;
    &::after {
        content: " ";
        display: block;
        width: 30px;
        height: 30px;
        margin: 8px;
        border-radius: 50%;
        border: 4px solid #fff;
        border-color: #fff transparent #fff transparent;
        animation: lds-dual-ring 1.2s linear infinite;
    }
    
    &_dark:after {
        border: 4px solid #1f1f1f;
        border-color: #1f1f1f transparent #1f1f1f transparent;
    }
}

@keyframes lds-dual-ring {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

.fadeIn {
    animation-name: fadeIn;
    animation-duration: 1s;
    animation-fill-mode: both;
}
@keyframes fadeIn {
    0% {opacity: 0;}
    100% {opacity: 1;}
} 


.background {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%;
}