@import "main-bx";

.calculation {
    &__inner {
        background-color: #171717;

        padding: 100px 0 190px;

        @include below(1440px) {
            padding: 70px 0;
        }

        @include below(1124px) {
            padding: 50px 0;
        }
    }

    &__title {
        padding-right: 200px;
        margin-bottom: 35px;
    }

    &__text {
        width: 100%;
        max-width: 300px;
        margin-left: 30px;
    }

    &__steps {
        width: 100%;
        max-width: 880px;
        margin-left: auto;
    }
}

@include below(1320px) {
    .calculation {
        &__steps {
            margin-left: 0;
            margin-top: 60px;
        }
    }
}

@include below(769px) {
    .calculation {
        &__inner {
            padding: 40px 0 60px;
        }

        &__text {
            margin-left: 0;
            max-width: 345px;
        }

        &__steps {
            margin-top: 90px;
        }
    }
}