@import "main-bx";

.title {
    @include fontProperty($accent_font, 500, 76px, 100%, $white);
    text-transform: uppercase;

    @include below(1440px) {
        font-size: 57px;
    }

    @include below(1200px) {
        font-size: 35px;
    }

    @include below(769px) {
        font-size: 30px;
    }

    &_dark {
        color: $color_dark1;
    }

    &_white {
        color: $white;
    }
}