@import "main-bx"; 

.cooperation {
    &__inner {
        background-image: url(../img/content/background/6.jpg);

        padding: 100px 0 188px;

        @include below(1440px) {
            padding: 70px 0;
        }

        @include below(1124px) {
            padding: 50px 0;
        }
    }

    &__head {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 100px;
        &-item {
            &:first-child {
                width: 60%;
            }

            &:last-child {
                width: 40%;
            }
        }

        @include below(1440px) {
            margin-bottom: 70px;
        }

        @include below(1124px) {
            margin-bottom: 50px;
        }
    }

    &__btn {
        color: $white;
        max-width: 282px;
        margin-left: auto;
    }
}

@include below(769px) {
    .cooperation {
        &__inner {
            padding: 40px 0 60px;
        }

        &__head {
            display: block;
            margin-bottom: 40px;
            &-item {
                &:first-child {
                    width: 100%;
                    margin-bottom: 40px;
                }
    
                &:last-child {
                    width: 100%;
                }
            }
        }

        &__btn {
            margin-left: 0;
        }
    }
}

@include below(600px) {
    .cooperation {
        &__btn {
            max-width: 100%;
        }
    }
}