@import "main-bx";

.cost {
    &__inner {
        background-image: url(../img/content/background/7.jpg);

        padding: 100px 0 120px;

        @include below(1440px) {
            padding: 70px 0;
        }

        @include below(1124px) {
            padding: 50px 0;
        }
    }

    &__title {
        padding-right: 180px;
        margin-bottom: 40px;
    }

    &__cards {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        &-top {
            position: relative;
            height: 562px;
            transition: $trns;
            border: 1px solid $color_dark1;
            overflow: hidden;
            padding: 40px;
        }

        &-bottom {
            height: 100px;
            padding: 20px;
            border-left: 1px solid $color_dark1;
            border-right: 1px solid $color_dark1;
            border-bottom: 1px solid $color_dark1;
            transition: $trns;
            opacity: 0;
        }

        &-btn {
            max-width: 200px;
            margin-left: auto;
        }

        &-sup {
            @include fontProperty($accent_font, 300, 18px, 120%, #7e7e7e)
        }

        &-title {
            @include fontProperty($accent_font, 700, 36px, 100%, $color_dark1);
            margin-bottom: 30px;
        }

        &-list {
            margin-bottom: 30px;
        }

        &-row {
            display: flex;
            align-items: flex-start;
            font-weight: 300;
            &:not(:last-child) {
                margin-bottom: 13px;
            }
            svg {
                width: 100%;
                max-width: 22px;
                height: 22px;
                margin-right: 16px;
            }
        }

        
    }

    &__item {
        width: 32%;
        cursor: pointer;
        transition: $trns;
        @media (min-width: 769px) {
            &:hover {
                background-color: $white;
                .cost__cards-top {
                    height: 700px;
                    overflow: visible;
                }
    
                .cost__cards-top_max {
                    height: 750px;
                }
    
                .cost__cards-bottom {
                    opacity: 1;
                }
                .cost__fragment {
                    opacity: 1;
                }
    
                .cost__rate-link {
                    opacity: 1;
                }
            }
        }
        
    }

    &__fragment {
        opacity: 0;
        transition: $trns;
        &-title {
            @include fontProperty($accent_font, 700, 20px, 130%, $color_dark1);
            margin-bottom: 20px;
        }
    }

    &__rate {
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: absolute;
        bottom: 0;
        right: 0;
        left: 0;
        padding: 40px;
        &-link {
            @include fontProperty($accent_font, 700, 14px, 130%, $color_dark1);
            letter-spacing: 0.05em;
            text-transform: uppercase;
            transition: $trns;
            opacity: 0;
            &:hover {
                opacity: 0.7;
            }
        }

        &-numb {
            @include fontProperty($accent_font, 700, 36px, 100%, $color_dark1);
        }

        &-text {
            @include fontProperty($accent_font, 300, 18px, 120%, #7e7e7e);
            text-align: right;
        }
    }
}


@include below(1320px) {
    .cost {
        &__item {
            width: 44%;
            margin: 0 25px;
        }

        &__cards {
            justify-content: center;
        }
    }
}

@include below(900px) {
    .cost {
        &__item {
            width: 70%;
            margin: 0;
        }
    }
} 


@include below(769px) {
    .cost {

        &__inner {
            padding: 40px 0 60px;
        }

        &__title {
            padding-right: 0;
        }
        
        &__item {
            width: 100%;
            margin-bottom: 20px;
            background-color: $white;
            
        }

        &__cards {
            &-top {
                padding: 30px 30px 30px 40px;
                height: auto !important;
            }

            &-bottom {
                opacity: 1;
            }

            &-btn {
                color: $white;
            }
        }

        &__fragment {
            opacity: 1;
        }

        &__rate {
            position: relative;
            padding: 0;
            display: block;
            padding-bottom: 40px;
            &-text {
                text-align: left;
                padding-left: 100px;
            }

            &-link {
                opacity: 1;
                position: absolute;
                right: 0;
                bottom: 0;
            }
        }
    }
} 


@include below(600px) {
    .cost {
        &__cards-btn {
            max-width: 100%;
        }
    }
} 