@import "main-bx";

.header {
	position: absolute;
    top: 30px;
    left: 0;
    right: 0;
    z-index: 1000;

    &__inner {
        display: flex;
        border: 1px solid #7E7E7E;
        transition: $trns;
    }

    &__logo {
        width: 100%;
        max-width: 195px;
        padding: 38px 42px;
        transition: $trns;

        &:hover {
            background-color: $white;

            .header__logo-icon {
                display: none;
            }

            .header__logo-icon_dark {
                display: block;
            }
        }
        &-icon {
            max-width: 115px;
            height: auto;
            &_dark {
                display: none;
            }
        }
    }

    &__menu {
        display: flex;
        flex: 1;
        border-left: 1px solid #7E7E7E;
        transition: $trns;
    }

    &__center {
        width: 100%;
        max-width: 880px;
        border-right: 1px solid #7E7E7E;
    }

    &__row {
        display: flex;
        align-items: center;
        flex: 1;
        &_justify_end {
            justify-content: flex-end;
            padding: 15px 30px;
            
        }
    }

    &__nav {
        padding: 0 30px;
        border-top: 1px solid #7E7E7E;

        @include below(1124px) {
            padding: 0 10px;
        }
    }

    &__right {
        width: 100%;
        max-width: 222px;
    }

    &__btn {
        width: 100%;
        text-align: center;
    }

    &__social {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 14px 0px;
        &-icon {
            margin: 0 8px;
        }
    }

    &__burger {
        position: relative;
        display: none;
        width: 30px;
        height: 30px;

        &-line {
            position: absolute;
            left: 0;
            display: block;
            width: 100%;
            height: 2px;
            background-color: $white;
            transition: $trns;
            &:first-child {
                top: 7px;
            }
            &:last-child {
                bottom: 7px;
            }
        }

        &.active {
            .header__burger-line:first-child {
                transform: rotate(45deg);
                top: auto;
            }

            .header__burger-line:last-child {
                transform: rotate(-45deg);
                bottom: auto;
            }
        }
    }
}

@include below(1024px) {
    .header {
        top: 0;
        &__inner {
            display: flex;
            justify-content: space-between;
            align-items: center;

            &.active {
                background-color: #0c0c0c;
                .header__menu {
                    transform: translateX(0);
                }
            }
        }
        &__burger {
            display: block;
        }

        &__logo {
            padding: 25px 42px;
        }
        &__menu {
            position: absolute;
            top: 0;
            left: 0;
            display: block;
            background-color: #0c0c0c;
            padding: 40px 0;
            width: 100%;
            max-width: 380px;
            transform: translateX(-200%);
        }

        &__center {
            display: flex;
            flex-direction: column-reverse;
            border: 0;
        }

        &__nav {
            border: 0;
            .nav__list {
                display: block;
                text-align: center;
            }
        }
        &__right {
            max-width: 100%;
        }
        &__btn {
            width: 100%;
        }

        &__row {
            flex-direction: column;
            padding: 34px 0;
            border-top: 1px solid #FFFFFF;
        }

        &__social {
            padding: 0;
            padding-top: 30px;
        }
    }
}

@include below(769px) {
    .header {
        &__logo {
            padding: 16px 25px;
        }

        &__menu {
            top: 67px;
        }

        &__nav {
            margin-bottom: 30px;
        }
    }
}