@import "main-bx";

.seo {

    &__inner {
        background-image: url(../img/content/background/5.jpg);

        padding: 45px 0 40px;

        

    }

    &__wrap {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
    }

    &__item {
        width: 40%;
        &:first-child {
            margin-right: 140px;
        }

        &:last-child {
            padding-right: 100px;
        }
    }

    &__title {
        @include fontProperty($accent_font, 700, 36px, 100%, $color_dark1);
        margin-bottom: 32px;
    }

    &__text {
        color: #7E7E7E;
    }

    &__btn {
        color: $color_dark1;
        margin-left: auto;
        max-width: 270px;
    }
}

@include below(900px) {
    .seo {
        &__item {
            width: 100%;
            &:first-child {
                margin-right: 0;
            }
        }
    }
}



@include below(769px) {
    .seo {

        &__inner {
            padding: 30px 0 40px;
        }
        &__title {
            font-size: 30px;
            margin-bottom: 24px;
        }

        &__text {
            margin-bottom: 50px;
        }
        &__item {
            width: 100%;
            &:last-child {
                padding-right: 0;
            }
        }
    }
}

@include below(600px) {
    .seo {
        &__btn {
            max-width: 100%;
        }
    }
}